.mainApp{

}

.mainApp header{
    height: 40px;
    margin: 0px;
}

.mainApp .menuIcon{
    width:32px;
    height: 32px;
    overflow: hidden;
    border-style: none;
    padding: 4px;
    cursor: pointer;
    -webkit-transition: 0.2s ease all;
    -o-transition: 0.2s ease all;
    transition: 0.2s ease all;
}

.mainApp .menuIcon.active{
    -webkit-transform: rotate(90deg);
        -ms-transform: rotate(90deg);
            transform: rotate(90deg);
    margin-left: calc(30vw - 32px);
}

.mainApp .menuIcon .bar{
    width:24px;
    height: 2px;
    background: rgba(255,255,255,0.9);
    border-radius: 4px;
    margin: 6px auto;
    -webkit-transition: 0.2s ease all;
    -o-transition: 0.2s ease all;
    transition: 0.2s ease all;
}

.mainApp .menuIcon:hover .bar {
    width: 20px;
    background: rgba(255,255,255,0.7);
    -webkit-transition: 0.1s ease all;
    -o-transition: 0.1s ease all;
    transition: 0.1s ease all;
}

.mainApp main{
    margin: 0px;
    width:calc(100vw);
    height: calc(100vh - 40px);
    background: rgba(255,255,255,0.9);
    overflow: hidden;
}

.mainApp nav{
    /*background: #1b184f, #3a8498);*/
    height: calc(100vh - 40px);
    width: 40px;
    position: fixed;
    top: 40px;
    left:0px;
    background: rgba(39,71,112,1);
    -webkit-transition: 0.15s ease all;
    -o-transition: 0.15s ease all;
    transition: 0.15s ease all;
    overflow: hidden;
    z-index: 100;
}

.mainApp .optionContainer{
    width: 75vw;
    height: 30px;
    margin-left: 50%;
    -webkit-transform: translate(-50%,0%);
        -ms-transform: translate(-50%,0%);
            transform: translate(-50%,0%);
    /*background: green;*/
}

.optionContainer button{
    width: 15vw;
    height: 20px;
    background: rgba(39,71,112,0.1);
    margin: 0px 1vw;
    border-radius: 30px;
    text-align: center;
}

.optionContainer button:hover{
    background: rgba(39,71,112,0.5);
    color:white;
}

.optionContainer button.selected{
    background: rgba(39,71,112,1);
    color:white;
}

.mainApp nav.open{
    width: 30vw;
}

.mainApp nav.closed{
    width: 0px;
}

.mainApp nav.open{
    opacity: 1;
    -webkit-transition: 0.15s ease all;
    -o-transition: 0.15s ease all;
    transition: 0.15s ease all;
}

.mainApp nav.closed{
    opacity: 0;
    -webkit-transition: 0.15s ease all;
    -o-transition: 0.15s ease all;
    transition: 0.15s ease all;
}

.mainApp nav a, .navGroupTitle{
    display: block;
    width: 100%;
    height: 34px;
    line-height: 34px;
    margin-top:3px;
    background: rgba(255,255,255,0.1);
}

.navGroupTitle{
    background: rgba(255,255,255,0.0);
    /*cursor: pointer;*/
    /*border-bottom-style: solid;*/
    /*border-bottom-color: rgba(255,255,255,0.8);*/
    /*border-bottom-width:1px;*/
    /*height: 30px !important;*/
    /*line-height: 30px;*/
}

.navGroup a{
    /*width: calc(100% - 10px) !important;*/
    /*margin-left: 10px;*/
}

.navGroupTitle:hover{
    background: rgba(255,255,255,0);
}

.mainApp nav a:hover{
    background: rgba(255,255,255,0.2);
}

body{
    overflow: hidden;
}

main{
    color: rgba(18,60,112,1);
}

@media only screen and (max-width: 501px) {

    .mainApp nav{

    }
    .mainApp nav.open{
        width: 100vw !important;
        height: calc(100vh - 40px) !important;
    }

    .mainApp nav.closed{
        height: 0vh !important;
        width: 100vw !important;
    }
    .mainApp .menuIcon.active{
        -webkit-transform: rotate(90deg);
            -ms-transform: rotate(90deg);
                transform: rotate(90deg);
        margin-left: 0px !important;
    }
}


