body {
  margin: 0;
  padding: 0;
    text-align: center;
    font-family: 'Raleway', sans-serif;
    font-weight: 200;
    overflow: hidden;
    position: fixed;
}

.App-background {
    background: -webkit-linear-gradient(46deg, #1b184f, #3a8498);
    background: -o-linear-gradient(46deg, #1b184f, #3a8498);
    background: linear-gradient(44deg, #1b184f, #3a8498);
    background-size: 400% 400%;
    -webkit-animation: AnimationName 10s ease infinite;
    animation: AnimationName 10s ease infinite;
    height: 100vh;
    width:100vw;
    /*padding: 0px 20px;*/
    margin:0px;
    padding: 0px;
    color: white;
    overflow: hidden;
    margin: 0;
    padding: 0;
}

.root{
    /*overflow: hidden;*/
    /*background: green;*/
    margin: 0;
    padding: 0;
}

@-webkit-keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}
@keyframes AnimationName {
    0%{background-position:0% 50%}
    50%{background-position:100% 50%}
    100%{background-position:0% 50%}
}

button {
    outline: none;
    border: none;
}

a{
    color: white;
    text-decoration: none;
}

h1{
    font-weight: 300;
}


