.center-frame{
    width:30vw;
    margin-left: 50%;
    top: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    padding: 20px 0px 0px 0px;
    position: absolute;
    background: rgba(200,200,200,0.2);
}

.center-frame header{
    height:10vh;
    width:100%;
    text-align: center;
    line-height:10vh;
}

.center-frame input{
    border-radius: 0%;
    left: 50%;
    width:80%;
    height: 5vh;
    text-align: center;
    border-style: none;
    background: rgba(200,200,200,0.0);
    border-bottom-style: solid;
    border-bottom-width: 1px;
    border-bottom-color: rgba(200,200,200,0.5);
    color: white;
    font-size: 15px;
    margin: 0px 0px;
    font-family: 'Raleway', sans-serif;
    font-weight: 200;
}

.center-frame input::-webkit-input-placeholder{
    color: rgba(200,200,200,0.5);
}

.center-frame input:-ms-input-placeholder{
    color: rgba(200,200,200,0.5);
}

.center-frame input::-ms-input-placeholder{
    color: rgba(200,200,200,0.5);
}

.center-frame input::placeholder{
    color: rgba(200,200,200,0.5);
}

.center-frame input + .inputBorder{
    background: rgba(255,255,255,1);
    -webkit-transform: translate(-50%,0);
        -ms-transform: translate(-50%,0);
            transform: translate(-50%,0);
    width:0%;
    margin-left: 50%;
    margin-top:-1px;
    margin-bottom: 0px;
    height:1px;
    -webkit-transition: 0.2s ease all;
    -o-transition: 0.2s ease all;
    transition: 0.2s ease all;
}

.center-frame input:focus + .inputBorder, .center-frame input + .inputBorderValid{
    background: rgba(255,255,255,1);
    -webkit-transform: translate(-50%,0);
        -ms-transform: translate(-50%,0);
            transform: translate(-50%,0);
    margin-left: 50%;
    width:40%;
    margin-top:-1px;
    margin-bottom: 0px;
    height:1px;
    -webkit-transition: 0.2s ease all;
    -o-transition: 0.2s ease all;
    transition: 0.2s ease all;
}

.center-frame input + .inputBorderValid{
    width:80%;
}

.center-frame button{
    border-radius: 0px;
    border-style: none;
    background: rgba(255,255,255,0.1);
    text-align: center;
    width:100%;
    height:10vh;
    margin-top: 30px;
    font-size: 15px;
    color: rgba(255,255,255,0.6);
    font-family: 'Raleway', sans-serif;
    font-weight:100;
    border-style: none;
    -webkit-transition: 0.2s ease all;
    -o-transition: 0.2s ease all;
    transition: 0.2s ease all;
}

.center-frame button:hover{
    background: rgba(255,255,255,0.2);
    color: rgba(255,255,255,1);
}

.center-frame button:focus{
    background: rgba(255,255,255,0.2);
    color: rgba(255,255,255,1);
}

/*.center-frame button:active{*/
    /*background: rgba(255,255,255,0.5);*/
    /*color: rgba(255,255,255,1);*/
/*}*/

.center-frame .busy{
    -webkit-animation: busy 2s ease infinite;
            animation: busy 2s ease infinite;
}

@-webkit-keyframes busy {
    0%{background: rgba(255,255,255,0.1);}
    50%{background: rgba(255,255,255,0.3)}
    100%{background: rgba(255,255,255,0.1);}
}

@keyframes busy {
    0%{background: rgba(255,255,255,0.1);}
    50%{background: rgba(255,255,255,0.3)}
    100%{background: rgba(255,255,255,0.1);}
}

.center-frame .error{
    background: rgba(255,100,100,0.1)
}

.center-frame .error, .center-frame .error:hover, .center-frame .error:focus{
    background: rgba(255,100,100,0.1) !important;
}


input:focus {outline:0;}

@media only screen and (max-width: 501px) {
    .center-frame{
        padding: 30vh 0 0 0;
        width: 100vw;
        height: 100vh;
    }
    .center-frame button{
        width: 80vw;
    }
}


